  color: white; }

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: #369B40; }
  h1 a:hover,
  h2 a:hover,
  h3 a:hover,
  h4 a:hover,
  h5 a:hover,
  h6 a:hover {
    color: #007a4b; }

a {
  font-weight: 700;
  transition: color 0.5s ease;
  text-decoration: none; }
  a:hover {
    font-weight: 700;
    text-decoration: underline; }

h1,
h3,
h2,
h4,
